import styled from 'react-emotion'

const StyledInput = styled('input')`
  width: 100%;
  height: 40px;
  font-size: 1em;
  border-radius: 0;
  border: none;
  background: rgba(255, 255, 255, 1);
  color: #333;
  padding-left: 10px;
`
export default StyledInput



